#all-columns {

  margin-right: 800px;
  padding: 0 100px 0 50px;

margin: auto;

}
#all-columns div {
  margin: auto;
  background-color: #34495e;
  border-radius: 5px;

  height: 100%;

  margin: auto;
}

#all-columns th {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  text-align: left;
}

#all-columns th:before {


  /* Display the counter named "step" in a decimal format */

  color: blue;

  /* Because there's an ancestor with relative positioning, this pseudo element will be positioned absolutely in relation to that ancestor */
  position: absolute;
  left: -16px;
}

#movies-row > div > table > tbody > tr:nth-child(n) > td:nth-child(3) {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  text-align: left;
}

#all-columns .custom-counter {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#all-columns .custom-counter li {
  counter-increment: step-counter;
  margin: 10px 10px 5px 10px;
}

.list-top-torrent {
  content: counter(step-counter);
  margin-right: 5px;
  font-size: 80%;
  background-color: rgb(24, 24, 24);
  color: white;
  font-weight: bold;
  padding: 3px 8px;
  border-radius: 3px;
  position: relative;
  bottom: 3px;
}

