/*2222*/

ul {
  border-radius: 5px;

  overflow: auto;
  /* this will hide the scrollbar in mozilla based browsers */
  overflow: -moz-scrollbars-none;
  scrollbar-width: none;
  /* this will hide the scrollbar in internet explorers */
  -ms-overflow-style: none;
}

ul::-webkit-scrollbar {
  left: 25px;
  width: 0 !important;
  display: none;
}

/*animation-lottie*/
.Animation-lottie {
  display: grid;
  justify-content: center;
  justify-self: center;
  justify-items: center;
  align-items: center;
  align-self: center;
  align-items: center;
  border: none;
  height: 10vh;
  width: 100vw;
}
