body,
html {
  font-family: "Ubuntu", sans-serif;

  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(36, 3, 3, 1) 50%,
    rgba(0, 0, 0, 1) 100%
