  overflow-x: hidden;
}

a {
  text-decoration: none !important;
  color: rgb(189, 54, 54);
}

a:hover {
  color: #2b0d0d;
}

#content {
  display: grid;

  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(250px, auto);

  grid-template-areas:
    "one one one one"
    "top-torrent top-torrent top-torrent top-torrent"
    "two two two two";

  grid-gap: 20px;

  overflow-x: hidden;
  overflow-y: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.one {
  grid-area: one;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;

  background-color: #272a3f;

  padding: 0;
  margin: 0;

  background-image: url("https://i.ibb.co/bKTmD0H/0c970c8b7986fdb4243c98126c6f9bd0.jpg");

  background-size: cover;
}

.one .text-field-container {
  padding: 0 5px 10px 20px;

  overflow-y: hidden; /* Hide vertical scrollbar */
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

.top-torrent {
  grid-area: top-torrent;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  padding: 0;
}

.two {
  grid-area: two;
}

.nested-rows-two {
  top: -400px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(200px, auto);
  grid-template-areas:
    "movies-row shows-row anime-row games-row"
    "movies-row shows-row anime-row games-row"
    "movies-row shows-row anime-row games-row"
    "movies-row shows-row anime-row games-row";



grid-gap: 60px;
}

.movies-row {
  grid-area: movies-row;
}

.anime-row {
  grid-area: anime-row;
}

.shows-row {
  grid-area: shows-row;
}

.games-row {
  grid-area: games-row;
}

.three {
  grid-area: three;

  display: none;
}

/* search field*/

/* text field*/

:root {
  --input-color: #99a3ba;
  --input-border: #cdd9ed;
  --input-background: #fff;
  --input-placeholder: #cbd1dc;
  --input-border-focus: #275efe;
  --group-color: var(--input-color);
  --group-border: var(--input-border);
  --group-background: #eef4ff;
  --group-color-focus: #fff;
  --group-border-focus: var(--input-border-focus);
  --group-background-focus: #678efe;
}

.form-field {
  display: block;
  width: 100%;
  padding: 8px 16px;
  line-height: 25px;
  font-size: 14px;
  font-weight: 500;
  font-family: inherit;
  border-radius: 25px;
  -webkit-appearance: none;
  color: rgb(37, 37, 37);
  border: 1px solid #cdd9ed;
  border: 1px solid var(--input-border);
  background: #fff;
  background: var(--input-background);
  transition: border 0.3s ease;
}
.form-field:-ms-input-placeholder {
  color: #cbd1dc;
  color: var(--input-placeholder);
}
.form-field::-webkit-input-placeholder {
  color: #cbd1dc;
  color: var(--input-placeholder);
}
.form-field::placeholder {
  color: #cbd1dc;
  color: var(--input-placeholder);
}
.form-field:focus {
  outline: none;
  border-color: #275efe;
  border-color: var(--input-border-focus);
}

.form-group {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 100%;
}
.form-group > span,
.form-group .form-field {
  white-space: nowrap;
  display: block;
}

.form-group > span:not(:first-child):not(:last-child),
.form-group .form-field:not(:first-child):not(:last-child) {
  border-radius: 25px;
}
.form-group > span:first-child,
.form-group .form-field:first-child {
  border-radius: 15px 0 0 15px;
}
.form-group > span:last-child,
.form-group .form-field:last-child {
  border-radius: 0 6px 6px 0;
}
.form-group > span:not(:first-child),
.form-group .form-field:not(:first-child) {
  margin-left: -1px;
}
.form-group .form-field {
  position: relative;
  z-index: 1;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  width: 1%;
  margin-top: 0;
  margin-bottom: 0;
}
.form-group > span {
  text-align: center;
  padding: 8px 12px;
  font-size: 14px;
  line-height: 25px;
  color: rgb(43, 43, 43);
  background: rgb(18, 116, 207);
  border: 1px solid rgb(197, 197, 197);
  transition: background 0.3s ease, border 0.3s ease, color 0.3s ease;
}
.form-group:focus-within > span {
  color: rgb(26, 26, 26);
  background: rgb(62, 115, 165);
  border-color: #275efe;
  border-color: var(--group-border-focus);
}

