/* go home*/

#link-go-home a:hover {
  cursor: pointer;
  color: rgb(185, 23, 23);
}
#link-go-home a {
  font-size: 80px;
  text-decoration: none;
  color: #d1d1d1;
}

#link-go-home .underline {
  position: relative;
}

#link-go-home .underline::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 2px;
  background-color: #850909;
  transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}

@media (hover: hover) and (pointer: fine) {
  #link-go-home .underline:hover::before {
    left: 0;
    right: auto;
    width: 100%;
  }
}

#link-go-home img {
  position: relative;
  height: 80px;

  width: auto;
}

